<template>
  <div id="article">
      <article v-for="(item,index) in articleList" :key="index">
        <h2>{{item.title}}</h2>
        <div class="time">
          <p class="data">{{item.time | dayTime}}</p>
          <p class="month_year"><span>{{item.time | monthTime}}月</span><span>{{item.time | yearTime}}</span></p>
        </div>
        <div class="show">
          <div class="img" :style="{backgroundImage: `url(${urlBase}${item.cover})`}">

          </div>
          <div class="des">{{item.des}}</div>

        </div>
        <div class="r-b">
          <P>浏览量： {{item.pv }}</P>
          <router-link :to="`/Article/${item._id}`">阅读全文</router-link>
        </div>
      </article>
  </div>
</template>

<script>
export default {
    name: "Article",
    data(){
      return {
        articleList: [],
        urlBase: process.env.NODE_ENV === "development"?"http://localhost:3000/":"",
      }
    },
    filters: {
    
    // 对年处理
    yearTime(value){
      let date = new Date(value)
      let YY = date.getFullYear();
      return YY
    },
    // 对月处理
    monthTime(value){
      let date = new Date(value)
      let MM = date.getMonth() + 1;
      MM = (MM < 10 ? "0" : "") + MM
      return MM
    },
      // 对日处理
    dayTime(value){
      let date = new Date(value)
      let DD = date.getDate();
      DD = (DD < 10 ? "0" : "") + DD
      return DD
    },
  },
    async created(){

      let {data} = await this.$axios({
        method: "GET",
        url: "/art/all",
      })
      if(data.code !== 0){
        return this.$message.error("参数错误")
      }
      this.articleList = data.data
    }
}
</script>

<style lang="less" scoped>
#article {
  user-select: none;
  article{
    position: relative;
    box-sizing: border-box;
    width: 100%;
    margin-bottom: 15px;
    box-shadow: 0 0 4px #ddd;
    padding: 25px 20px;
    background-color: #fff;
    font-family: "Quicksand","Microsoft YaHei",sans-serif;
    cursor: pointer;
    h2{
    margin-bottom: 15px;
    font-size: 20px;
    letter-spacing: 3px;
    line-height: 24px;
    border-left: 5px solid #73b899;
    text-indent: 10px;
    }
    div.time{
      position: absolute;
      top: 10px;
      right: 10px;
      width: 60px;
      .data{
        font-weight: bold;
        font-size: 30px;
        text-align: center;
        color: #73b899;
      }
      .month_year{
        span:nth-child(1){
          float: left;
        }
        span:nth-child(2){
          float: right;
        }
      }
    }
    div.show{
      display: flex;
      width: 100%;
      height: 200px;
      div.img{
        box-sizing: border-box;
        width: 50%;
        background-size: cover;
        background-position: center;
        padding-right: 1%;
      }
      div.des{
        box-sizing: border-box;
        width: 50%;
        padding-left: 1%;
      }
    }
    .r-b{
      position: absolute;
      right: 20px;
      bottom: 20px;
      p{
        position: relative;
        top: -10px;
        color: #aaa;
        font-size: 12px;
      }
      a{
        padding: 10px 20px;
        background-color: #409eff;
        color: #fff;
        transition: opacity .3s;
        &:hover{
          opacity: .8;
        }
      }
    }

  
  }
  
  
}
</style>